.tip-button {
    width: 304px;
    height: 89px;
    background-size: 100%;
    /*background-image: url('images/1_pay_mm_off.png');*/
    cursor: pointer;
    display: inline-block;
  }
  
  .left-container {
    height: 100%;
    display: inline-block;
    width: 50%;
