    margin-bottom: 15px;
  }
  
  .users-logo {
    height: 25px;
    width: 25px;
    display: inline-block;
    vertical-align: middle;
  }
  
  .default-font {
    font-family: 'Montserrat', sans-serif;
  }
  
  .default-font-color {
    color: #484d79;
  }
  
  .stream-header-container {
    margin-top: 15px;
    margin-left: 100px;
    margin-right: 100px;
  }
  
